import React, { useContext, useState } from 'react';
import { AppContext } from '../store/context';
import getLangPack from '../LangPack';
import {convertToStringT, roundDigit, getWindDir, convertWindSpeedVal, convertDate, convertTime} from '../Lib';
import GetWeatherIcon from './GetWeatherIcon';
import WindDir from './Icons/WindDir';
import '../styles/HourlyForecast.scss';

export default function ForecastHourly(){
  const { state, dispatch } = useContext(AppContext);

  const lang = getLangPack(state.lang.code);
  const weather = state.weather.data;
  let hourSection = null;
  if (weather) {
    const dailyFrc: Array<any> = weather.hourly.slice(1, 26);

    hourSection = dailyFrc.map((d, i) => {
      const convertedTime = convertTime(d.dt);
      const dt = new Date(d.dt * 1000);
      const day = convertedTime === '00:00' ? <div>{lang.lib.dayShort[dt.getDay()]}</div>: null;
      return <div key={d.dt} className='Hourly-Forecast-Wrapper'>
            <div className='Hourly-Forecast-Header'>
                {day}
                <span className='Hourly-forecast-Date-Val'>
                  {convertedTime}
                </span>
            </div>
            <div className='Hourly-Forecast-WeatherDesc'>
              <GetWeatherIcon iconCode={d.weather[0].icon} wrapper='Weather-Icon-Wrapper-Forecast-Hour'/>
            </div>
            <div className='Hourly-Forecast-Temp'>
                {convertToStringT(d.temp, state.uni.gen)}
            </div>
        </div>;
    });
  }
  return (
    <div className="ForecastHour-wrapper">
      <div className='ForecastHour-Container'>
        {hourSection}
      </div>
    </div>
  );
}